.input_wrapper {
  display: flex;
  flex-direction: column;
}

.input_password {
  position: relative;
}

.label {
  font-weight: 600;
  font-size: 1rem;
  line-height: 1.5rem;
  color: var(--primary-color);
}

.input {
  outline: 2px solid transparent;
  outline-offset: 2px;
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.5rem;
  padding: 5px 15px;
  width: 100%;
  border: 1px solid var(--border-color);
  border-radius: 5px;
}

.eye {
  position: absolute;
  bottom: -15%;
  right: 3%;
  transform: translateY(-25%);
  color: var(--primary-color);
  cursor: pointer;
}

.eye svg {
  width: 16px;
  height: 16px;
}

.error {
  font-size: .9rem;
  line-height: 1.25rem;
  font-weight: 600;
  padding: 0 15px;
  color: #ef4444;
  text-align: left;
}

@media screen and (max-width: 768px) {
  .eye {
    bottom: -3%;
  }
}